/* src/components/Dashboard.css */
.dashboard {
    position:relative;
    top: 0;
    left: 0;
    height: 100vh;
    width: 250px;
    background-image: url("../images/universe.jpg");
    background-position:center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #ecf0f1;
    padding: 30px;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    overflow-y: auto;
    margin: 10px;
    border-radius: 5px;
  }
  
  .dashboard h1 {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .dashboard form {
    display: flex;
    flex-direction: column;
  }
  
  .dashboard label {
    margin-bottom: 15px;
  }
  
  .dashboard input {
    margin-top: 5px;
    padding: 8px;
    border: 1px solid #bdc3c7;
    border-radius: 4px;
    width: 100%;
  }
  
  .dashboard button {
    margin-top: 10px;
    padding: 10px;
    background-color: #e74c3c;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .dashboard button:hover {
    background-color: #c0392b;
  }
  